.poster {
  width: 25%;
  height: 50%;
  top: 20px;
  position: relative;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.details {
  margin-bottom: 50px;
}

.details-content {
  width: 75%;
  margin: 5% auto 20px auto;
  padding: 2% 5%;
  background-color: white;
  border-radius: 10px;
  letter-spacing: 0.8px;
}

.container {
  width: 80%;
  margin: 0 auto;
}

.cast-image {
  width: 100%;
  height: 100%;
}

.cast-image img {
  width: 100%;
  height: 100%;
}

.casts {
  display: grid;
  grid-template-columns: 100px auto;
  height: 75%;
  grid-column-gap: 5%;
  margin-top: 20px;
}

.casts-main {
  display: grid;
  grid-template-columns: 33.33% 33.33% 33.33%;
  grid-column-gap: 5%;
}

.cast-details span {
  font-size: 15px;
}

.cast-details a {
  text-decoration: none;
}

.cast-details a:hover {
  text-decoration: underline;
}

@media screen and (max-width: 900px) {
  .casts-main {
    grid-template-columns: 50% 50%;
  }
}

@media screen and (max-width: 550px) {
  .casts-main {
    grid-template-columns: 100%;
  }
}

@media screen and (max-width: 880px) {
  .poster {
    width: 30%;
  }
}

@media screen and (max-width: 750px) {
  .poster {
    width: 35%;
  }
}

@media screen and (max-width: 600px) {
  .poster {
    width: 40%;
  }
  .details-content {
    margin-top: 10%;
  }
}

@media screen and (max-width: 460px) {
  .poster {
    width: 50%;
  }
}

@media screen and (max-width: 350px) {
  .poster {
    width: 70%;
  }
}

.movie-navbar {
  width: 85%;
  margin: 0 auto 20px auto;
  display: grid;
  background-color: #535353;
  grid-template-columns: 50% 50%;
}

.movie-navbar div {
  padding: 15px 0;
  text-align: center;
  transition: 0.1s;
  background-color: #535353;
  color: white;
}

.movie-navbar .selection-active {
  background-color: #2a2a2a;
}

.movie-navbar div:hover {
  background-color: #c2c2c2;
  transition: 0.1s;
  cursor: pointer;
  color: #535353;
}
